'use strict';

// MODULES //

import { isPrimitive as isString } from '@stdlib/assert-is-string';
import hasOwnProp from '@stdlib/assert-has-own-property';
import database from '@stdlib/error-tools-database';
import inverse from '@stdlib/utils-object-inverse';
import format from '@stdlib/error-tools-fmtprodmsg';


// VARIABLES //

var db;


// MAIN //

/**
* Returns a two-character error identifier corresponding to a provided error message.
*
* @param {string} msg - error message
* @throws {TypeError} must provide a string
* @returns {(string|null)} two-character error identifier
*
* @example
* var v = msg2id( 'invalid option. `%s` option must be an array. Option: `%s`.' );
* // returns '8Z'
*/
function msg2id( msg ) {
	if ( !isString( msg ) ) {
		throw new TypeError( format( "0Nx3B,IL", msg ) );
	}
	if ( !db ) {
		db = inverse( database() );
	}
	if ( hasOwnProp( db, msg ) ) {
		return db[ msg ];
	}
	return null;
}


// EXPORTS //

export default msg2id;
